.ReactSwipeButton * {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */
  -khtml-user-select: none; /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently not supported by any browser */
}

.ReactSwipeButton {
  width: 100%;
  height: 50px;
  position: relative;
}

.rsbContainer {
  width: 100%;
  height: 100%;
  background: #eee;
  border-radius: 50px;
  position: relative;
  box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  z-index: 5;
}

.rsbContainerUnlocked {
  width: 50% !important;
  margin-left: 25%;
  transition: 0.5s;
  cursor: default;
}

.rsbContainerUnlocked .rsbcSlider {
  left: 100% !important;
  width: 100% !important;
  cursor: default;
  pointer-events: none;
}

.rsbContainerUnlocked .rsbcSliderArrow,
.rsbContainerUnlocked .rsbcSliderCircle {
  display: none;
}

.rsbcSlider {
  width: 100%;
  height: 50px;
  z-index: 100;
  position: absolute;
  left: 50px;
  margin-left: -100%;
  cursor: pointer;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
  border-radius: 50px;
}

.rsbcSliderText {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  line-height: 50px;
  text-align: center;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  letter-spacing: 2px;
  color: #fff;
  font-size: 13px;
}

.rsbcSliderArrow {
  float: left;
  position: absolute;
  transform: rotate(45deg);
  border: 2px solid #fff;
  height: 8px;
  width: 8px;
  top: calc(50% - 4px);
  right: 24px;
  border-left-color: transparent;
  border-bottom-color: transparent;
  transform-origin: center;
  z-index: 10;
}

.rsbcSliderCircle {
  position: absolute;
  right: 0;
  background: green;
  top: 0;
  height: 50px;
  width: 50px;
  border-radius: 100%;
}

.rsbcText {
  position: absolute;
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  color: #aaa;
  font-family: "Lucida Sans", sans-serif;
  font-size: 13px;
  letter-spacing: 4px;
  text-transform: uppercase;
  height: 50px;
  line-height: 50px;
}
